import { http, HttpResponse } from "msw";
import { faker } from "@faker-js/faker";
import route from "../../route";
import language from "../../../resources/languages/en_US";

// Interfaces
import { User, FilterUser } from '../../../interfaces/pages/user-management/user'

// Storages
import { getUsers, saveUsers } from "../storages/user.storage";

const ROUTE = `${route.VERSION}/${route.USER_MANAGEMENT}/user/`
const { Mock: MockLanguage } = language

const findAllUser = [
    http.post(ROUTE + 'all', async ({ request }) => {
        // filters from body
        const body = (await request.json().catch(() => ({}))) as Partial<FilterUser>;
        const { firstName, lastName, email } = body;

        let users = getUsers();

        if (users.length === 0) {
            users = Array.from({ length: 10 }).map(() => ({
                id: faker.string.uuid(),
                firstName: faker.person.firstName(),
                lastName: faker.person.lastName(),
                email: faker.internet.email(),
            }));
            saveUsers(users);
        }

        // filtering
        const filteredUsers = users.filter((user) => {
            let isValid = true;

            if (firstName) {
                isValid =
                    isValid &&
                    user.firstName.toLowerCase().includes(firstName.toLowerCase());
            }

            if (lastName) {
                isValid =
                    isValid &&
                    user.lastName.toLowerCase().includes(lastName.toLowerCase());
            }

            if (email) {
                isValid = isValid && user.email === email;
            }

            return isValid;
        });

        return HttpResponse.json(filteredUsers);
    }),
];

const findAllPaginateUser = [
    http.post(ROUTE + 'paginate', async ({ request }) => {
        const url = new URL(request.url);

        // pagination from query
        const page = Number(url.searchParams.get('page') ?? 1);
        const limit = Number(url.searchParams.get('limit') ?? 10);

        // filters from body
        const body = (await request.json().catch(() => ({}))) as Partial<FilterUser>;
        const { firstName, lastName, email } = body;

        let users = getUsers();

        if (users.length === 0) {
            users = Array.from({ length: 10 }).map(() => ({
                id: faker.string.uuid(),
                firstName: faker.person.firstName(),
                lastName: faker.person.lastName(),
                email: faker.internet.email(),
            }));
            saveUsers(users);
        }

        // filtering
        const filteredUsers = users.filter((user) => {
            let isValid = true;

            if (firstName) {
                isValid =
                    isValid &&
                    user.firstName.toLowerCase().includes(firstName.toLowerCase());
            }

            if (lastName) {
                isValid =
                    isValid &&
                    user.lastName.toLowerCase().includes(lastName.toLowerCase());
            }

            if (email) {
                isValid = isValid && user.email === email;
            }

            return isValid;
        });

        // pagination
        const start = (page - 1) * limit;
        const end = start + limit;
        const paginatedUsers = filteredUsers.slice(start, end);

        return HttpResponse.json({
            docs: paginatedUsers,
            limit,
            page,
            totalDocs: filteredUsers.length,
            totalPages: Math.ceil(filteredUsers.length / limit),
        });
    }),
];

const findByIdUser = [
    http.get(ROUTE + ':id', ({ params }) => {
        const users = getUsers();

        const index = users.findIndex(user => user.id === params.id);
        if (index === -1) {
            return HttpResponse.json({ message: MockLanguage.notFound }, { status: 404 });
        }

        return HttpResponse.json(users[index]);
    }),
];

const createUser = [
    http.post(ROUTE, async ({ request }) => {
        const body = await request.json() as Omit<User, "id">;

        if (!body.firstName || !body.lastName || !body.email) {
            return HttpResponse.json(
                { message: MockLanguage.badRequest },
                { status: 400 }
            );
        }

        const users = getUsers();
        const newUser: User = {
            id: crypto.randomUUID(),
            ...body,
        };

        users.push(newUser);
        saveUsers(users);

        return HttpResponse.json(newUser, { status: 201 });
    }),
];

const updateUser = [
    http.put(ROUTE + ':id', async ({ params, request }) => {
        const body = await request.json() as Partial<User>;
        const users = getUsers();

        const index = users.findIndex(user => user.id === params.id);
        if (index === -1) {
            return HttpResponse.json({ message: MockLanguage.notFound }, { status: 404 });
        }

        users[index] = { ...users[index], ...body };
        saveUsers(users);

        return HttpResponse.json(users[index]);
    }),
];

const removeUser = [
    http.delete(ROUTE + ':id', ({ params }) => {
        const users = getUsers().filter(user => user.id !== params.id);
        saveUsers(users);
        return HttpResponse.json({});
    }),
];

export default [
    ...findAllUser,
    ...findAllPaginateUser,
    ...findByIdUser,
    ...createUser,
    ...updateUser,
    ...removeUser,
];