import { http, HttpResponse } from "msw";
import { faker } from "@faker-js/faker";
import route from "../../route";

// Interfaces
import { File as FileType } from '../../../interfaces/pages/file-management/file'

// Storages
import { getFiles, saveFiles } from "../storages/file.storage";

const ROUTE = `${route.VERSION}/${route.File_MANAGEMENT}/file/`

const uploadFile = [
    http.post(ROUTE + 'upload', async ({ request }) => {
        const formData = await request.formData();
        const file = formData.get('file') as File;

        const files = getFiles();

        const record: FileType = {
            id: crypto.randomUUID(),
            name: file.name,
            size: file.size,
            type: file.type,
            createdAt: Date.now(),
        };

        files.push(record);
        saveFiles(files);

        await new Promise(r => setTimeout(r, 1500));

        return HttpResponse.json(record);
    }),
];

export default [
    ...uploadFile,
];